import React from "react";
import Typewriter from 'typewriter-effect';
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import EmailIcon from "@material-ui/icons/Email";
import GithubIcon from "@material-ui/icons/GitHub";
import "../styles/Home.css";

function Home() {
  const openInNewTab = url => {
    window.open(url, '_blank', 'noopener,noreferrer');
  };
  return (
    <div className="home">
      <div className="about">
        <Typewriter
          options={{
            strings: "<h2> Hi, I'm Tegar Nugroho</h2>",
            autoStart: true,
            cursor: '',
            delay: 150,
          }}
        />
        <div className="prompt">
          <p>
            A skilled Flutter Developer with 4 years of experience in building robust and user-friendly Mobile Applications.
          </p>
          <LinkedInIcon
            onClick={() => {
              openInNewTab("https://www.linkedin.com/in/tegaranugroho/");
            }}
          />
          <EmailIcon
            onClick={() => {
              openInNewTab(
                "mailto:tegaranugroho@gmail.com?subject=Subject&body=Hi%20Tegar"
              );
            }}
          />
          <GithubIcon
            onClick={() => {
              openInNewTab("https://github.com/tegarnugroho");
            }}
          />
        </div>
      </div>
    </div>
  );
}

export default Home;
